import Navigation from "@/components/Navigation";
import Footer from "@/components/Footer";

export default {
    name: "App",
    data() {
        return {
            scrollPosition: null,
        };
    },
    components: {
        Navigation,
        Footer,
    },
    computed: {
        isAuthenticated() {
            if (this.$route.name === "Walk") {
                return false;
            }
            return true;
        },
    },
    methods: {
        updateScroll() {
            this.scrollPosition = window.scrollY;
        },
    },
    mounted() {
        window.addEventListener("scroll", this.updateScroll);
    },
};
